@import url(https://fonts.googleapis.com/css?family=Abel&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.App {
  text-align: center;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  font-family: 'Abel', sans-serif;
  text-rendering: optimizeLegibility;
  color: white;
  letter-spacing: 0em; }

img.background {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: 50% 50%; }

.content {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 1;
  background-color: rgba(26, 27, 50, 0.8);
  overflow-y: scroll; }
  .content .container {
    padding: 25px 60px;
    position: relative;
    box-sizing: border-box;
    height: auto; }

.segment {
  margin: auto;
  padding-bottom: 30px;
  max-width: 880px; }
  .segment--first {
    padding-top: 30px; }
  .segment__text {
    font-size: 17px;
    max-width: 36em;
    margin: 36px auto;
    font-weight: 400;
    line-height: 1.4em;
    -webkit-text-size-adjust: 100%;
        -ms-text-size-adjust: 100%;
            text-size-adjust: 100%;
    white-space: pre-wrap; }
  .segment__links {
    text-transform: uppercase;
    letter-spacing: 0.2em;
    font-size: 17px;
    margin-top: -0.8em; }
  .segment__link {
    padding-top: 0.8em;
    padding-bottom: 0.8em;
    -webkit-transition: color 100ms ease-in-out;
    transition: color 100ms ease-in-out;
    text-decoration: none;
    color: #ffbf00;
    display: block;
    margin-top: 0;
    margin-bottom: 15px;
    line-height: 17px; }
    .segment__link:hover {
      color: rgba(255, 191, 0, 0.6); }
    .segment__link--last {
      margin-bottom: 0; }

img.logo {
  max-height: 200px; }

.social__wrapper {
  background-color: #f2ae02;
  width: 28px;
  height: 28px;
  box-sizing: content-box;
  display: inline-block;
  border-radius: 15px;
  color: white;
  margin: 0 4px;
  -webkit-transition: background-color 170ms ease-in-out, color 170ms ease-in-out;
  transition: background-color 170ms ease-in-out, color 170ms ease-in-out; }
  .social__wrapper--dim {
    background-color: rgba(242, 174, 2, 0.5);
    color: rgba(255, 255, 255, 0.4); }
  .social__wrapper:hover {
    background-color: #f2ae02;
    color: white; }

.social__icon {
  line-height: 28px;
  vertical-align: bottom;
  font-size: 15px; }

